import React from "react";
import { graphql } from "gatsby";

function BlogPostTemplate(props) {
    // const post = props.data.markdownRemark;
    return (
        <div>
            <h1>{props.title}</h1>
            <div dangerouslySetInnerHTML={{ __html: props.html }} />
        </div>
    );
}

export default BlogPostTemplate;

